import React from 'react';

export const MiniCard= (props) => {
    // props expects a data property which is an array of objects
    // each object should have the 'unit' and 'label' property
    // data = [{unit: 10, label: 'asd'}]
    const { data } = props;

    return (
        <div className='mini-card-container'>
            {
                data.map(d => {
                    return <div className='mini-card'>
                        <div className='mini-card-unit'>
                            {d.unit}
                        </div>
                        <div className='mini-card-label'>
                            {d.label}
                        </div>
                    </div>
                })
            }
        </div>
    )
}
