import { Shirts, SupplierPrice, ShippingPerOrder } from './constant';
import data from './data.json';
import format from 'format-number';

export const formatter = format({ prefix: "₱ ", truncate: 0 });

const pushData = (shirtsData, data, shirt) => {
    switch (shirt) {
        case 'PAYAPA':
            shirtsData.PAYAPA.push(data)
            break;
        case 'PADAYON':
            shirtsData.PADAYON.push(data)
            break;
        case 'PINILI (White)':
            shirtsData.PINILI_W.push(data)
            break;
        case 'PINILI (Black)':
            shirtsData.PINILI_B.push(data)
            break;
        case 'MAHALAGA':
            shirtsData.MAHALAGA.push(data)
            break;
        default:
            break;
    }
}

export const getData = () => {
  let orderCount = 0;
  let ordersTotal = 0;
    const shirtsData = {
        PAYAPA: [], 
        PADAYON: [],
        PINILI_W: [],
        PINILI_B: [],
        MAHALAGA: [],
    };
  let currentAmount = 0;
  let pendingAmount = 0;
  let shippingSubTotal = 0;
  for (const orderData of data.ordersWithCustomer) {
    const { order } = orderData;
    const name = order.billingPerson.name;
    let isPaid = order.paymentStatus === 'ACCEPTED';

    for (const item of order.items) {
        for (let i = 0; i < item.quantity; i++) {
            // PINILI had choices before (black/white) but it was later on separated
            let color;
            if (item.selectedOptions.values.Color) {
                color = item.selectedOptions.values.Color.text;
            }

            orderCount++;
            ordersTotal += item.price;
            const itemName = color? `${item.name} (${color})`: item.name;
            const orderItem = {
                orderNo:orderCount,
                name,
                item: itemName,
                price: item.price,
                sizeModifier: parseFloat(item.selectedOptions.values.Size.modifier),
                paid: isPaid
            } 
            pushData(shirtsData, orderItem, orderItem.item);
            isPaid? currentAmount += item.price : pendingAmount += item.price;
        }
    }

    shippingSubTotal += order.shippingOption.shippingRate;
  }
  ordersTotal += shippingSubTotal;

  return {
    ordersTotal,
    orderCount,
    currentAmount,
    pendingAmount,
    shirtsData,
    shippingSubTotal
  }
}

// this method expects an array of objects with property: price
export const getSumPrice = (data) => {
    const sum = data.reduce((prev,cur) => { 
        return prev + cur.price
    }, 0);

    return sum;
}

export const getCapitalTotal = (ordersList) => {
    const capital = ordersList.reduce((prev, cur) => {
        const supplierData = SupplierPrice[Shirts[cur.item]];

        // formula: supplier price + extra size (if available)
        return prev + (supplierData.normalSizes + cur.sizeModifier);
    }, 0);

    return capital;
}

// there's a few things here to consider:
// 1. the shipping (since we removed the shipping, some orders has shipping thoug so we'll handle those)
// 2. we'll set the shipping per order (By USER) to PHP 40
export const getTotalIncome = (options = {considerShipping: false, paidOnly: false}) => {
    let totalIncome = 0;
    // we'll have to use the original data here
    for (const { order } of data.ordersWithCustomer) {
        if (options.paidOnly && order.paymentStatus !== 'ACCEPTED') {
            continue;
        }

        let shipping = options.considerShipping ? 40:0;
        let orderTotal = order.total;
        // some orders has shipping (first few orders). 
        // If they provided the shipping, we'll use that instead.
        if (options.considerShipping && order.shippingOption.shippingRate > 0) {
            shipping = order.shippingOption.shippingRate;
        }

        let deductibles = shipping;
        for (const item of order.items) {
            const color = item.selectedOptions.values.Color;
            const itemName = color? `${item.name} (${color.text})`: item.name;
            const supplierPrice = SupplierPrice[Shirts[itemName]];
            const sizeModifier = parseFloat(item.selectedOptions.values.Size.modifier) 
            
            deductibles += (supplierPrice.normalSizes + sizeModifier) * item.quantity;

            // less first the supplier price each item
            //const itemNet = (item.productPrice * item.quantity) - deductibles;
            //orderTotal += itemNet;
        }

        // then we'll less the deductibles (supplier price, etc);
        totalIncome += orderTotal - deductibles;
    }

    return totalIncome;
}

export const getOrdersByUser = () => {
    const orders = [];

    for (const { order } of data.ordersWithCustomer) {
        const userOrder = {
            id: order.vendorOrderNumber,
            name: order.billingPerson.name,
            email: order.email,
            paid: order.paymentStatus === 'ACCEPTED',
            shipping: parseFloat(order.shippingOption.shippingRate),
            createdAt: order.creationDate,
            items: order.items.map(item => ({
                shirt: item.name, 
                quantity: item.quantity, 
                size: item.selectedOptions.values.Size.text
            }))
        }
        orders.push(userOrder);
    }

    return orders;
}

export const isOrderSale = (order) => {
    const orderDate = new Date(parseInt(order.createdAt + "000"));
    const saleStartDate = new Date('10/10/2024 00:00:00');
    const saleEndDate = new Date('10/20/2024 23:59:00:00');
    return orderDate >= saleStartDate && orderDate <=saleEndDate;
}
